import { CustomerType } from '../../config/types';
import { CustomerActionTypes, CustomerActionTypeTypes } from '../Actions/customerActions';

type InitialStateType = {
  customer: CustomerType[];
};

const initialState: InitialStateType = {
  customer: [],
};

const customerReducer = (state = initialState, action: CustomerActionTypes): InitialStateType => {
  switch (action.type) {
    case CustomerActionTypeTypes.CREATE_CUSTOMER:
      return {
        ...state,
        customer: [...state.customer, action.payload],
      };
    case CustomerActionTypeTypes.LOAD_CUSTOMERS:
      return {
        ...state,
        customer: action.payload,
      };
    default:
      return state;
  }
};

export default customerReducer;
