import axios from 'axios';
import { Dispatch } from 'redux';
import { authHeader, url } from '../../config/config';
import { CustomerType } from '../../config/types';
import { createCustomer, CustomerActionTypes, loadCustomers } from './customerActions';

export const createCustomerByRequest = (customer: CustomerType) => {
  return async (dispatch: Dispatch<CustomerActionTypes>) => {
    try {
      const response = await axios.post(`${url}/customers`, customer, { headers: authHeader() });
      dispatch(createCustomer(response.data));
    } catch (e) {
      console.log(e);
    }
  };
};

export const loadCustomersByRequest = () => {
  return async (dispatch: Dispatch<CustomerActionTypes>) => {
    try {
      const response = await axios.get(`${url}/customers`, { headers: authHeader() });
      const customers = response.data;
      dispatch(loadCustomers(customers));
    } catch (e) {
      console.log(e);
    }
  };
};
