import React, { useCallback, useEffect, useMemo } from 'react';
import { Link } from 'react-router-dom';
import '../auth.scss';
import { useDispatch, useSelector } from 'react-redux';
import { login } from '../../../redux/Actions/thunkUserActions';
import logo from '../../../assets/images/logo.png';
import { RootState } from '../../../redux/Reducers/rootReducer';
import 'antd/dist/antd.min.css';
import { Button, Form, Input } from 'antd';
import { LockOutlined, MailOutlined } from '@ant-design/icons';
import ErrorAlert from '../../Alerts/ErrorAlert/ErrorAlert';
import { clearAlert } from '../../../redux/Actions/alertActions';

type UserDataLoginType = {
  username: string;
  password: string;
};

const Login: React.FC = () => {
  const error = useSelector((state: RootState) => state.alert.error);
  const dispatch = useDispatch();

  useEffect(() => {
    dispatch(clearAlert());
  }, []);

  const initialValues: UserDataLoginType = useMemo(() => {
    return {
      username: '',
      password: '',
    };
  }, []);

  const onSubmit = useCallback(
    (e) => {
      dispatch(login(e.username, e.password));
    },
    [dispatch]
  );

  return (
    <>
      <div className="auth">
        <div className="auth__wrap">
          <div className="auth__content">
            <div className="auth__image-wrap">
              <img src={logo} alt="Logo" />
            </div>
            <Form
              name="normal_login"
              className="login-form"
              initialValues={initialValues}
              onFinish={onSubmit}
            >
              <Form.Item
                name="username"
                rules={[{ required: true, message: 'Пожалуйста введите действительную почту!' }]}
              >
                <Input
                  prefix={<MailOutlined className="site-form-item-icon" />}
                  type="email"
                  placeholder="Адрес почты"
                />
              </Form.Item>
              <Form.Item
                name="password"
                rules={[{ required: true, message: 'Пожалуйста, введите пароль!' }]}
              >
                <Input.Password
                  prefix={<LockOutlined className="site-form-item-icon" />}
                  type="password"
                  placeholder="Пароль"
                />
              </Form.Item>
              {error && (
                <Form.Item>
                  <ErrorAlert error={error} />
                </Form.Item>
              )}
              <Form.Item>
                <Button type="primary" htmlType="submit" className="login-form-button">
                  Войти
                </Button>
                Впервые? <Link to={'/auth/signup'}>Зарегистрироваться!</Link>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Login;
