import axios from 'axios';
import { authHeader, url } from '../config/config';

class calcAPI {
  async createCalc(calc: any, id: string) {
    return await axios
      .post(`${url}/calculations/seframe/${id}`, calc, { headers: authHeader() })
      .then((res) => {
        return res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  }

  async editAddressReportCalc(id: string, report: any) {
    return await axios
      .post(`${url}/calculations/update/${id}`, report, { headers: authHeader() })
      .then((res) => {
        return res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  }
}

export default new calcAPI();
