import React, { useEffect, useState } from 'react';
import { PlusOutlined, BookOutlined } from '@ant-design/icons';
import { Button, List } from 'antd';
import './CustomerCardPage.scss';
import AddCalcModal from './AddCalcModal/AddCalcModal';
import { Link, useParams } from 'react-router-dom';
import reportAPI from '../../../API/reportAPI';
import { ReportType } from '../../../config/types';
import moment from 'moment';

const CustomerCardPage = () => {
  const urlParams = useParams();
  const [isModalVisible, setIsModalVisible] = useState<boolean>(false);
  const [reports, setReports] = useState<ReportType[]>([]);

  const showModal = () => {
    setIsModalVisible(true);
  };

  const closeModal = () => {
    setIsModalVisible(false);
  };

  const deleteReport = (id: string) => {
    reportAPI.deleteReport(id).then(() => {
      setReports(reports.filter((item) => item.id !== id));
    });
  };
  const copyReportCalc = (id: string) => {
    reportAPI.copyReportCalc(id).then((data) => {
      if (data) setReports((prevState) => [...prevState, data]);
    });
  };
  useEffect(() => {
    if (urlParams.id) {
      reportAPI.getAllReports(urlParams.id).then((data) => {
        setReports(data);
      });
    }
  }, [urlParams.id]);

  return (
    <div className="customer-page">
      <div className="customer-page__header">
        <div className="title">Карточка клиента</div>
      </div>
      <Button onClick={showModal} className="btn" icon={<PlusOutlined />} size="large">
        Создать расчет
      </Button>
      <List
        className="customer-calc"
        itemLayout="horizontal"
        dataSource={reports}
        renderItem={(item, index) => (
          <List.Item>
            <List.Item.Meta
              avatar={<BookOutlined style={{ fontSize: '150%' }} />}
              title={<Link to={`/customer/report/${item.id}`}>Расчет №{index + 1}</Link>}
              description={moment.utc(item.createdDate).format('MM.DD.YYYY, h:mm')}
            />
            <div className="status">
              {item.stateName ? (
                (item.stateName === 'Не актуален' && (
                  <span className="status__error"> {item.stateName}</span>
                )) ||
                (item.stateName === 'Актуален' && (
                  <span className="status__success"> {item.stateName}</span>
                ))
              ) : (
                <span className="status__signed">{item.stateName}</span>
              )}
            </div>
            <Button onClick={() => copyReportCalc(item.id)} type="link">
              Копировать
            </Button>
            <Button onClick={() => deleteReport(item.id)} type="link">
              Удалить
            </Button>
          </List.Item>
        )}
      />
      <AddCalcModal visible={isModalVisible} onCancel={closeModal} />
    </div>
  );
};

export default CustomerCardPage;
