import React, { useEffect, useState } from 'react';
import './ReportCalcPage.scss';
import { Button, Table } from 'antd';
import reportAPI from '../../../API/reportAPI';
import { Link, useParams } from 'react-router-dom';
import { ReportType } from '../../../config/types';
import moment from 'moment';

const ReportCalcPage = () => {
  const [dataTable, setDataTable] = useState<any[]>([]);
  const urlParams = useParams();
  const [reportParams, setReportParams] = useState<ReportType>({
    id: '',
    number: '',
    createdDate: '',
    constructionObjectAddress: '',
    stateName: '',
  });

  useEffect(() => {
    if (urlParams.id) {
      reportAPI.getReportById(urlParams.id).then((data) => {
        if (data) setDataTable(data);
      });
      reportAPI.getReportParams(urlParams.id).then((data) => {
        if (data) setReportParams(data);
      });
    }
  }, [urlParams.id]);

  const dataSource = (item: any, index: number) => {
    const tempSource = [];
    let count = 1;
    tempSource.push({
      key: (count++).toString(),
      type: 'Внешние стены',
    });
    item.externalWalls.map((it: any) => {
      it.key = (count++).toString();
      tempSource.push(it);
    });
    tempSource.push({
      key: (count++).toString(),
      type: 'Внутренние стены',
    });
    item.internalWalls.map((it: any) => {
      it.key = (count++).toString();
      tempSource.push(it);
    });
    tempSource.push({
      key: (count++).toString(),
      type: 'Перекрытия',
    });
    item.overlaps.map((it: any) => {
      it.key = (count++).toString();
      tempSource.push(it);
    });
    return tempSource;
  };

  const columns = [
    {
      title: '',
      dataIndex: 'type',
      key: 'type',
      render: (text: string) => {
        if (text === 'Внешние стены' || text === 'Внутренние стены' || text === 'Перекрытия')
          return <div className="report-table__subtitle">{text}</div>;
        else return <>{text}</>;
      },
    },
    {
      title: 'Материал',
      dataIndex: 'material',
      key: 'material',
    },
    {
      title: 'Единицы измерения',
      dataIndex: 'measurementUnit',
      key: 'measurementUnit',
    },
    {
      title: 'Количество',
      dataIndex: 'amount',
      key: 'amount',
    },
    {
      title: 'Стоимость материалов',
      dataIndex: 'totalCost',
      key: 'totalCost',
    },
  ];
  const editStateCalc = () => {
    if (urlParams.id) {
      reportAPI.editStateCalc(urlParams.id).then((data) => {
        setReportParams(data);
      });
    }
  };
  return (
    <div className="report-page">
      <div className="report-page__header">
        <div className="title">Расчет</div>
        {reportParams.stateName === 'Не актуален' && (
          <>
            <div className="status">
              Статус:
              <span className="status__error"> {reportParams.stateName}</span>
            </div>
            <Button onClick={editStateCalc} className="btn" size="middle">
              Актуализировать
            </Button>
          </>
        )}
        {reportParams.stateName === 'Актуален' && (
          <>
            <div className="status">
              Статус:
              <span className="status__success"> {reportParams.stateName}</span>
            </div>
            <Button onClick={editStateCalc} className="btn" size="middle">
              Деактуализовать
            </Button>
          </>
        )}
      </div>
      <div className="report-page__info">
        <div className="datetime">
          Дата:
          {reportParams.createdDate && (
            <span> {moment.utc(reportParams.createdDate).format('MM.DD.YYYY, h:mm')} </span>
          )}
          Адрес объекта:
          <span> {reportParams.constructionObjectAddress}</span>
        </div>
        <Link to={`/customer/report/${urlParams.id}/frame`}>Редактировать</Link>
      </div>
      <div className="report-page__content">
        {dataTable.map((item: any, index: number) => {
          return (
            <Table
              className="report-table"
              key={index}
              title={() => {
                return <h3>Результат расчета {index + 1} этажа</h3>;
              }}
              dataSource={dataSource(item, index)}
              columns={columns}
              size="small"
              pagination={false}
              summary={() => {
                return (
                  <>
                    <Table.Summary.Row>
                      <Table.Summary.Cell className="report-table__subtitle" index={1}>
                        Итого стоимость материалов
                      </Table.Summary.Cell>
                      <Table.Summary.Cell index={2} />
                      <Table.Summary.Cell index={3} />
                      <Table.Summary.Cell index={4} />
                      <Table.Summary.Cell className="report-table__subtitle" index={5}>
                        {item.totalCost}
                      </Table.Summary.Cell>
                    </Table.Summary.Row>
                  </>
                );
              }}
            />
          );
        })}
      </div>
    </div>
  );
};

export default ReportCalcPage;
