import React, { useCallback, useEffect, useMemo } from 'react';
import '../auth.scss';
import { Link } from 'react-router-dom';
import { signup } from '../../../redux/Actions/thunkUserActions';
import { useDispatch, useSelector } from 'react-redux';
import { Button, Form, Input } from 'antd';
import logo from '../../../assets/images/logo.png';
import { RootState } from '../../../redux/Reducers/rootReducer';
import { LockOutlined, MailOutlined, UserOutlined } from '@ant-design/icons';
import ErrorAlert from '../../Alerts/ErrorAlert/ErrorAlert';
import { clearAlert } from '../../../redux/Actions/alertActions';
import SuccessAlert from '../../Alerts/SuccessAlert/SuccessAlert';

type UserDataType = {
  username: string;
  email: string;
  password: string;
  confirm: string;
};

const Signup: React.FC = () => {
  const initialValues: UserDataType = useMemo(() => {
    return {
      username: '',
      email: '',
      password: '',
      confirm: '',
    };
  }, []);
  const dispatch = useDispatch();
  const error = useSelector((state: RootState) => state.alert.error);
  const success = useSelector((state: RootState) => state.alert.success);

  useEffect(() => {
    dispatch(clearAlert());
  }, []);

  const onSubmit = useCallback(
    (userData: UserDataType) => {
      dispatch(clearAlert());
      dispatch(signup(userData.email, userData.password, userData.username));
    },
    [dispatch]
  );

  return (
    <>
      <div className="auth">
        <div className="auth__wrap">
          <div className="auth__content">
            <div className="auth__image-wrap">
              <img src={logo} alt="Logo" />
            </div>
            <Form
              name="normal_login"
              className="login-form"
              layout="vertical"
              scrollToFirstError
              initialValues={initialValues}
              onFinish={onSubmit}
            >
              <Form.Item
                name="username"
                rules={[{ required: true, message: 'Пожалуйста, введите имя!' }]}
              >
                <Input
                  prefix={<UserOutlined className="site-form-item-icon" />}
                  placeholder="Имя"
                />
              </Form.Item>
              <Form.Item
                name="email"
                rules={[{ required: true, message: 'Пожалуйста введите действительную почту!' }]}
              >
                <Input
                  prefix={<MailOutlined className="site-form-item-icon" />}
                  type="email"
                  placeholder="Адрес почты"
                />
              </Form.Item>
              <Form.Item
                name="password"
                rules={[{ required: true, message: 'Пожалуйста, введите пароль!' }]}
              >
                <Input.Password
                  prefix={<LockOutlined className="site-form-item-icon" />}
                  type="password"
                  placeholder="Пароль"
                />
              </Form.Item>
              <Form.Item
                name="confirm"
                dependencies={['password']}
                hasFeedback
                rules={[
                  { required: true, message: 'Пароль не подтверждён!' },
                  ({ getFieldValue }) => ({
                    validator(_, value) {
                      if (!value || getFieldValue('password') === value) {
                        return Promise.resolve();
                      }
                      return Promise.reject(new Error('Пароли не совпадают!'));
                    },
                  }),
                ]}
              >
                <Input.Password
                  prefix={<LockOutlined className="site-form-item-icon" />}
                  type="password"
                  placeholder="Подтвердите пароль"
                />
              </Form.Item>
              {success && (
                <Form.Item>
                  <SuccessAlert message={success} />
                </Form.Item>
              )}
              {error && (
                <Form.Item>
                  <ErrorAlert error={error} />
                </Form.Item>
              )}
              <Form.Item>
                <Button type="primary" htmlType="submit" className="login-form-button">
                  Зарегистрироваться
                </Button>
                Уже есть аккаунт? <Link to={'/auth'}>Войти!</Link>
              </Form.Item>
            </Form>
          </div>
        </div>
      </div>
    </>
  );
};

export default Signup;
