import React from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from '../../../redux/Reducers/rootReducer';
import { logoutCurrentUser } from '../../../redux/Actions/userActions';
import { Link } from 'react-router-dom';
import './Header.scss';
import logo from '../../../assets/images/logo.png';
import logoutImg from '../../../assets/images/logout.png';

const Header = () => {
  const dispatch = useDispatch();

  const isAuth = useSelector((state: RootState) => state.user.isAuth);
  const user = useSelector((state: RootState) => state.user.currentUser);

  const logout = () => {
    dispatch(logoutCurrentUser());
  };

  return (
    <div className="header">
      <div className="container">
        <div className="header__content">
          <div className="header__logo">
            <Link to="/">
              <img src={logo} alt="Logo" />
              <span>Мы серьезная компания</span>
            </Link>
          </div>
          <div className="user">
            {isAuth ? (
              <>
                <div className="user__caption">Вы вошли как:</div>
                <div className="user__name">{user.username}</div>
                <div className="user__pos">Менеджер по менеджменту</div>
              </>
            ) : (
              <>
                <Link to="/auth/signup">Зарегистрироваться</Link>
                <Link to="/auth">Войти</Link>
              </>
            )}
          </div>
          <div onClick={logout} className="header__logout">
            <img src={logoutImg} alt="Выйти из аккаунта" />
            <span>Выйти из</span>
            <span>аккаунта</span>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Header;
