        osbExternalWall: '',
        steamWaterproofingExternalWall: '',
        windscreenExternalWall: '',
        insulationExternalWall: '',
        osbInternalWall: '',
        overlapThickness: '',
        osbOverlap: '',
        steamWaterproofingOverlap: '',
        windscreenOverlap: '',
        insulationOverlap: '',
