import { CustomerType } from '../../config/types';

export enum CustomerActionTypeTypes {
  CREATE_CUSTOMER = 'CREATE_CUSTOMER',
  LOAD_CUSTOMERS = 'LOAD_CUSTOMERS',
}

export type CustomerActionTypes = CreateCustomerType | LoadCustomersType;

export type CreateCustomerType = {
  type: CustomerActionTypeTypes.CREATE_CUSTOMER;
  payload: CustomerType;
};
export const createCustomer = (customer: CustomerType): CreateCustomerType => {
  return {
    type: CustomerActionTypeTypes.CREATE_CUSTOMER,
    payload: customer,
  };
};

export type LoadCustomersType = {
  type: CustomerActionTypeTypes.LOAD_CUSTOMERS;
  payload: CustomerType[];
};
export const loadCustomers = (customers: CustomerType[]): LoadCustomersType => {
  return {
    type: CustomerActionTypeTypes.LOAD_CUSTOMERS,
    payload: customers,
  };
};
