import axios from 'axios';
import { setCurrentUser, UserActionTypes, userLoading } from './userActions';
import { Dispatch } from 'redux';
import { url } from '../../config/config';
import { ErrorType } from '../../config/types';
import { AlertActionTypes, clearAlert, setErrorAlert, setSuccessAlert } from './alertActions';

export const signup = (email: string, password: string, name: string) => {
  return async (dispatch: Dispatch<UserActionTypes | AlertActionTypes>) => {
    dispatch(clearAlert());
    try {
      //Костыль
      const response = await axios.post(`${url}/users/register`, {
        firstName: name,
        lastName: name,
        secondName: name,
        phone: name,
        email: email,
        login: email,
        password: password,
      });
      dispatch(setSuccessAlert(response.data.message));
    } catch (e) {
      const u = e as ErrorType;
      dispatch(setErrorAlert(u.response.data.message));
    }
  };
};

export const login = (email: string, password: string) => {
  return async (dispatch: Dispatch<UserActionTypes | AlertActionTypes>) => {
    try {
      const response = await axios.post(`${url}/api/v1/auth/login`, {
        username: email,
        password: password,
      });
      const { username } = response.data;

      localStorage.setItem('username', response.data.username);
      localStorage.setItem('token', response.data.token);
      dispatch(
        setCurrentUser({
          username,
        })
      );
      dispatch(clearAlert());
    } catch (e) {
      const u = e as ErrorType;
      dispatch(setErrorAlert(u.response.data.message));
    }
  };
};
export const auth = () => {
  return async (dispatch: Dispatch<UserActionTypes | AlertActionTypes>) => {
    dispatch(userLoading(true));
    try {
      if (localStorage.getItem('token') === null || localStorage.getItem('username') === null)
        return dispatch(userLoading(false));
      const username = localStorage.getItem('username');
      dispatch(
        setCurrentUser({
          username,
        })
      );
      /* const response = await axios.get(`${url}/api/v1/auth/login`, AuthorizationHeaderConfig);
      const { userName } = response.data;
      dispatch(
        setCurrentUser({
          userName,
        })
      );
      localStorage.setItem('token', response.data.token);*/
      dispatch(userLoading(false));
    } catch (e) {
      const u = e as ErrorType;
      //dispatch(setAuthError(u.response.data.message));
      dispatch(setErrorAlert(u.response.data.message));
      dispatch(userLoading(false));
      localStorage.removeItem('token');
    }
  };
};
