import React from 'react';
import { Breadcrumb } from 'antd';
import { Link, useLocation, useParams } from 'react-router-dom';

const Breadcrumbs = () => {
  const urlParams = useParams();
  const breadcrumbMaps: any = {
    [`/customer/${urlParams.id}`]: `Карточка клиента`,
    [`/customer/report/${urlParams.id}`]: `Расчет`,
    [`/customer/report/${urlParams.id}/frame`]: `Каркас`,
  };
  const location = useLocation();
  const pathSnippets = location.pathname.split('/').filter((i) => i);
  const extraBreadcrumbItems = pathSnippets.map((_, index) => {
    const url = `/${pathSnippets.slice(0, index + 1).join('/')}`;
    if (breadcrumbMaps[url]) {
      return (
        <Breadcrumb.Item key={url}>
          <Link to={url}>{breadcrumbMaps[url]}</Link>
        </Breadcrumb.Item>
      );
    }
  });
  const breadcrumbItems = [
    <Breadcrumb.Item key="customer">
      <Link to="/">Клиенты</Link>
    </Breadcrumb.Item>,
    extraBreadcrumbItems,
  ];
  return <Breadcrumb>{breadcrumbItems}</Breadcrumb>;
};

export default Breadcrumbs;
