import React, { useEffect } from 'react';
import './App.css';
import { BrowserRouter, Navigate, Route, Routes } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { RootState } from './redux/Reducers/rootReducer';
import { auth } from './redux/Actions/thunkUserActions';
import AppLoading from './components/AppLoading/AppLoading';
import Login from './components/Auth/LogIn/Login';
import Signup from './components/Auth/SignUp/Signup';
import GuestRoute from './components/HOC/GuestRoute';
import RequireAuth from './components/HOC/RequireAuth';
import Layouts from './components/Layout/Layout';
import MainPage from './components/Layout/MainPage/MainPage';
import CustomerCardPage from './components/Layout/CustomerCardPage/CustomerCardPage';
import CalcPage from './components/Layout/CalcPage/CalcPage';
import ReportCalcPage from './components/Layout/ReportCalcPage/ReportCalcPage';

function App() {
  const isAuth = useSelector((state: RootState) => state.user.isAuth);
  const userLoading = useSelector((state: RootState) => state.user.isLoading);
  const dispatch = useDispatch();
  useEffect(() => {
    dispatch(auth());
  }, [dispatch]);

  return (
    <>
      {userLoading ? (
        <AppLoading />
      ) : (
        <BrowserRouter>
          <Routes>
            <Route path="/auth">
              <Route
                index
                element={
                  <GuestRoute isAuth={isAuth}>
                    <Login />
                  </GuestRoute>
                }
              />
              <Route path="signup" element={<Signup />} />
            </Route>
            <Route
              path="/"
              element={
                <RequireAuth isAuth={isAuth}>
                  <Layouts />
                </RequireAuth>
              }
            >
              <Route index element={<MainPage />} />
              <Route path="customer/:id" element={<CustomerCardPage />} />
              <Route path="customer/report/:id/frame" element={<CalcPage />} />
              <Route path="customer/report/:id" element={<ReportCalcPage />} />
            </Route>
          </Routes>
        </BrowserRouter>
      )}
    </>
  );
}

export default App;
