import React from 'react';
import { Modal } from 'antd';
import './AddCalcModal.scss';
import { useNavigate, useParams } from 'react-router-dom';
import frame from '../../../../assets/images/Frame.png';
import groundwork from '../../../../assets/images/groundwork.png';
import roof from '../../../../assets/images/roof.png';
import reportAPI from '../../../../API/reportAPI';
import moment from 'moment';

interface EditPlaylistModalProps {
  visible: boolean;
  onCancel: () => void;
}

const AddCustomerModal: React.FC<EditPlaylistModalProps> = ({ visible, onCancel }) => {
  const urlParams = useParams();
  const navigate = useNavigate();
  const initialValues = {
    number: 3453451,
    createdDate: moment().format('YYYY-MM-DDThh:mm:ss'),
    constructionObjectAddress: '',
  };
  const addReport = () => {
    if (urlParams.id) {
      reportAPI.createReportByCustomer(urlParams.id, initialValues).then((data) => {
        navigate(`/customer/report/${data.id}/frame`);
      });
    }
  };
  return (
    <Modal visible={visible} title="Выбор конструктивного элемента" onCancel={onCancel} footer={''}>
      <div className="structural">
        <div className="structural__list">
          <div onClick={addReport} className="structural__link">
            <span>Каркас</span>
            <img src={frame} alt="Каркас" />
          </div>
          <div className="structural__link">
            <span>Фундамент</span>
            <img src={groundwork} alt="Каркас" />
          </div>
          <div className="structural__link">
            <span>Крыша</span>
            <img src={roof} alt="Каркас" />
          </div>
        </div>
      </div>
    </Modal>
  );
};

export default AddCustomerModal;
