import React, { useCallback, useMemo } from 'react';
import { Button, Form, Input, Modal } from 'antd';
import { CustomerType } from '../../../../config/types';
import { useDispatch } from 'react-redux';
import { createCustomerByRequest } from '../../../../redux/Actions/thunkCustomerActions';

interface EditPlaylistModalProps {
  visible: boolean;
  onCancel: () => void;
  //onCreate: (data: any) => void;
}

const AddCustomerModal: React.FC<EditPlaylistModalProps> = ({ visible, onCancel }) => {
  const dispatch = useDispatch();
  const initialValues: CustomerType = useMemo(() => {
    return {
      firstName: '',
      lastName: '',
      secondName: '',
      phone: '',
      email: '',
      address: '',
    };
  }, []);
  const onSubmit = useCallback(
    (data) => {
      //onCreate(data);
      dispatch(createCustomerByRequest(data));
    },
    [dispatch]
  );
  return (
    <Modal visible={visible} title="Добавить клиента" onCancel={onCancel} footer={''}>
      <Form
        name="normal_login"
        className="login-form"
        initialValues={initialValues}
        onFinish={onSubmit}
      >
        <Form.Item
          name="lastName"
          rules={[{ required: true, message: 'Пожалуйста, введите фамилию!' }]}
        >
          <Input placeholder="Фамилия" />
        </Form.Item>
        <Form.Item
          name="firstName"
          rules={[{ required: true, message: 'Пожалуйста, введите имя!' }]}
        >
          <Input placeholder="Имя" />
        </Form.Item>
        <Form.Item
          name="secondName"
          rules={[{ required: true, message: 'Пожалуйста, введите отчество!' }]}
        >
          <Input placeholder="Отчество" />
        </Form.Item>
        <Form.Item
          name="phone"
          rules={[{ required: true, message: 'Пожалуйста, введите телефон!' }]}
        >
          <Input type="tel" placeholder="Телефон" />
        </Form.Item>
        <Form.Item name="email" rules={[{ required: true, message: 'Пожалуйста, введите email!' }]}>
          <Input type="email" placeholder="E-mail" />
        </Form.Item>
        <Form.Item
          name="address"
          rules={[{ required: true, message: 'Пожалуйста, введите адрес!' }]}
        >
          <Input placeholder="Адрес" />
        </Form.Item>
        {/* {error && (
          <Form.Item>
            <ErrorAlert error={error} />
          </Form.Item>
        )}*/}
        <Form.Item>
          <Button type="primary" htmlType="submit" className="login-form-button">
            Добавить
          </Button>
        </Form.Item>
      </Form>
    </Modal>
  );
};

export default AddCustomerModal;
